<template lang="pug">

.works(v-scroll="scrollResize")
	.fade(:class="{active: activeFade, fixed: fixedFade}" @click="worksFixed = false")
	.list(v-if="mobile")
		v-item(v-for="item in works" :data="item" :key="item.img")
	v-scroll(v-else :params="{number: slides.length}" @changeView="changeView")
		template(v-for="(slide, index) in slides" #[index])
			v-item(v-for="item in slide" :data="item" :key="item.img")


</template>

<script>
import { scroll } from 'plugins/directives'
import Scroll from 'temp/scroll'
import Item from './item'

export default {
	mounted(){
		this.checkDevice()
		window.addEventListener('resize', () => this.checkDevice())
	},
	data(){
		return {
			works: [
				{
					img: 'cyprus',
					link: 'https://invest-in-cyprus.com/',
					name: 'Инвестиции в недвижимость на Кипре',
					tags: [
						'creator',
						'frontend'
					]
				},
				{
					img: 'emirates',
					link: 'https://invest-in-emirates.com/',
					name: 'Инвестиции в недвижимость в Дубае',
					tags: [
						'creator',
						'frontend'
					]
				},
				{
					img: 'hotel',
					link: 'https://hotelapartments.ru/',
					name: 'Аппартаменты в центре Москвы',
					tags: [
						'creator',
						'frontend'
					]
				},
				{
					img: 'event',
					link: 'https://invest-in-cyprus.com/event/',
					name: 'Встреча по поводу получения гражданства в ЕС и инвестиций в недвижимость на Кипре',
					tags: [
						'creator',
						'frontend'
					]
				},
				{
					img: 'catalog',
					link: 'https://mosdevelopment.net/invest2/',
					name: 'Подбор новых жилых комлексов в Москве для инвестиций',
					tags: [
						'creator',
						'frontend',
						'backend'
					]
				},
				{
					img: 'shop',
					link: '/shop',
					name: 'Интернет-магазин по продаже продуктов оптом (недоделан, ожидаю макет дизайна и подключение со стороны 1С)',
					tags: [
						'creator',
						'vue',
						'frontend',
						'backend'
					]
				},
				{
					img: 'kypitedom',
					link: 'https://kypitedom.com/',
					name: 'Элитная загородная недвижимости в Москве',
					tags: [
						'support',
						'addition',
						'part-creator',
						'frontend',
						'backend'
					]
				},
				{
					img: 'hamovnyki',
					link: 'https://hamovnyki.ru/',
					name: 'Элитная недвижимость в Хамовниках',
					tags: [
						'support',
						'addition',
						'frontend',
					]
				},
				{
					img: 'moscowcitysale',
					link: 'https://moscowcitysale.ru/',
					name: 'Официальный сайт по продаже апартаментов и офисов в комплексе Москва-Сити',
					tags: [
						'support',
						'addition',
						'frontend',
						'backend'
					]
				},
				{
					img: 'kupipenthouse',
					link: 'https://kupipenthouse.ru/',
					name: 'Каталог пентхаусов в Москве',
					tags: [
						'support',
						'addition',
						'frontend'
					]
				},
				{
					img: 'osobnyaki',
					link: 'https://osobnyaki.com/',
					name: 'Особняки в Москве',
					tags: [
						'support',
						'addition',
						'frontend'
					]
				},
				{
					img: 'kupiteloft',
					link: 'https://kupiteloft.ru/',
					name: 'Каталог лофт-аппартаментов в Москве',
					tags: [
						'support',
						'frontend'
					]
				},
			],
			activeFade: false,
			fixedFade: false,
			dataCount: 6,
			mobile: false,
			last: {
				scroll: 0,
				time: 0
			}
		}
	},
	methods: {
		scrollResize(e){
			if (!this.mobile || Math.abs(this.last.scroll - this.$el.scrollTop) < 300)
				return
			this.last.time = e.timeStamp
			setTimeout(() => {
				if (this.last.time == e.timeStamp)
					this.$store.commit('worksResize', this.works.length)
			}, 200)
		},
		checkDevice(){
			let width = window.innerWidth
			this.mobile = false
			if (width <= 800) this.mobile = true
			else if (width <= 1200) this.dataCount = 4
			else this.dataCount = 6
		},
		changeView(){
			setTimeout(() => this.$store.commit('worksResize', this.works.length), 500)
		}
	},
	computed: {
		slides: {
			get(){
				let slides = []

				for (let i = 0; i < this.works.length; i+=this.count)
					slides[i/this.count] = this.works.slice(i, i+this.count)

				return slides
			}
		},
		worksFixed: {
			get(){
				return this.$store.state.worksFixed
			},
			set(val){
				this.$store.commit('worksFixed', val)
			}
		},
		count: {
			get(){
				if (!this.dataCount) this.checkDevice()
				return this.dataCount
			}
		}
	},
	watch: {
		worksFixed(val) {
			if (val) {
				this.fixedFade = true
				setTimeout(() => this.activeFade = true, 20)
			}
			else {
				this.activeFade = false
				setTimeout(() => this.fixedFade = false, 400)
			}
		}
	},
	components: {
		'v-item': Item,
		'v-scroll': Scroll
	},
	mixins: [scroll]
}

</script>

<style lang="stylus">

.works
	.scrollblock
		.view
			display flex
			flex-wrap wrap
			align-content center
			padding 0 30px
		.nav
			.next
				top calc(50% + 260px)
			.back
				bottom calc(50% + 270px)

@media screen and (max-width: 800px)
	.works
		.scrollblock
			.nav
				.next
					top calc(50% + 215px)
					bottom auto
				.back
					bottom calc(50% + 215px)
					top auto

</style>

<style lang="stylus" scoped>

.works
	position relative
	height 100%
	height 100vh
	width 1080px
	margin auto

.fade
	opacity 0
	transition opacity .4s
	display none
	&.fixed
		display block
		position fixed
		z-index 200
		width 100%
		height 100%
		width 100vw
		height 100vh
		top 0
		left 0
		background-color rgba(#000, .7)
		cursor pointer
	&.active
		opacity 1

@media screen and (max-width: 1200px)
	.works
		width 720px
@media screen and (max-width: 800px)
	.works
		width 320px
		overflow auto
		display flex
		flex-direction column
		align-items center

</style>