<template lang="pug">

.item(:class="{fixed, active}" :style="{height: coords.height}")
	.container(@click="show" :style="{top: coords.top, left: coords.left}")
		.img(:style="{backgroundImage: 'url(/img/works/'+data.img+'.jpg)'}")
		.tags(v-if="data.tags")
			.tag.creator(v-if="data.tags.includes('creator')") создатель
			.tag.part-creator(v-if="data.tags.includes('part-creator')") участие в создании
			.tag.addition(v-if="data.tags.includes('addition')") дополнение
			.tag.support(v-if="data.tags.includes('support')") поддержка
			.tag.frontend(v-if="data.tags.includes('frontend')") frontend
			.tag.backend(v-if="data.tags.includes('backend')") backend
			//- .tag.vue(v-if="data.tags.includes('vue')") vue.js
		.text {{data.name}}
		a.link(:href="data.link" target="_blank") Перейти на сайт

</template>

<script>

export default {
	mounted(){
		this.resize()
		this.coords.height = this.container.offsetHeight + 'px'
	},
	data(){
		return {
			fixed: false,
			active: false,
			coords: {
				top: null,
				left: null,
				height: null
			}
		}
	},
	methods: {
		show(){
			if (this.fixed) return
			this.resize()
			this.fixed = true
			this.worksFixed = true
			setTimeout(() => this.active = true, 20)
			
		},
		hide(){
			this.resize()
			this.active = false
			setTimeout(() => this.fixed = false, 400)
		},
		resize(){
			this.coords.top = this.$el.getBoundingClientRect().top + 'px'
			this.coords.left = this.$el.getBoundingClientRect().left + 'px'
		}
	},
	computed: {
		container(){
			return this.$el.querySelector('.container')
		},
		worksFixed: {
			get(){
				return this.$store.state.worksFixed
			},
			set(val) {
				this.$store.commit('worksFixed', val)
			}
		},
		worksResize(){
			return this.$store.state.worksResize
		}
	},
	watch: {
		worksFixed(val){
			if (this.fixed && val === false) this.hide()
		},
		worksResize(val){
			if (val) {
				this.resize()
				this.$store.commit('worksResize', -1)
			}
		}
	},
	props: ['data']
}

</script>

<style lang="stylus" scoped>

.item
	transition transform .5s
	position relative
	width 300px
	&:nth-child(3n+1),&:nth-child(3n+2)
		margin-right 60px
	&:nth-child(1), &:nth-child(2), &:nth-child(3)
		margin-bottom 50px
	&:hover
		transform scale(1.08)
	.container
		width 300px
		cursor pointer
		transition left .4s, top .4s, width .4s
	
	.img
		width 100%
		height 155px
		border-radius 2px
		background-size cover
		background-position center
		position relative
		transition height .4s
		z-index 2
	.text
		font-size 18px
		text-align center
		margin-top 10px
		transition margin-top .4s, font-size .4s
	.link
		font-size 25px
		margin-top 10px
		text-align center
		color #42aaff
		display none
		opacity 0
		transition opacity .4s, transform .3s
		&:hover
			transform scale(1.1)
	.tags
		position absolute
		z-index 1
		width 100%
		transition opacity .1s .3s, top .4s
		display none
		top 100px
		opacity 0
		text-align center
		.tag
			display inline-block
			vertical-align top
			font-size 16px
			padding 0 5px
			border-radius 5px
			margin-right 10px
			margin-bottom 10px
			&:last-child
				margin-right 0
			&.creator
				background-color #66ff00
				color #000
			&.support
				background-color #c70000
			&.addition
				background-color #fde910
				color #000
			// &.vue
			// 	color #35495e
			// 	background linear-gradient(60deg, #41b883 75%, #35495e 75%, #35495e 85%, #41b883 85%)
			// 	color #ffffff
			&.part-creator
				background repeating-linear-gradient(120deg, #66ff00, #66ff00 10px, #fde910 10px, #fde910 20px)
				color #000
			&.frontend
				background-color #ccc
				color #000
			&.backend
				background-color #555

	&.fixed
		transition none
		&:hover
			transform none
		.tags
			display block
		.container
			position fixed
			z-index 201
			cursor auto
		.link
			display block

	&.active
		.container
			left calc(50% - 350px) !important
			top calc(45% - 250px) !important
			left calc(50vw - 350px) !important
			top calc(45vh - 250px) !important
			width 700px
		.img
			height 365px
		.tags
			opacity 1
			top 375px
			transition opacity .1s, top .4s
		.text
			margin-top 40px
			font-size 23px
		.link
			opacity 1

@media screen and (max-width: 1200px)
	.item
		&:nth-child(3n+1), &:nth-child(3n+2)
			margin-right 0
		&:nth-child(2n+1)
			margin-right 60px
		&:nth-child(1), &:nth-child(2)
			margin-bottom 30px
@media screen and (max-width: 800px)
	.item
		width 260px
		margin-bottom 30px
		&:nth-child(2n+1)
			margin-right 0
		&:nth-child(1),&:nth-child(2), &:nth-child(3)
			margin-bottom 30px
		&:first-child
			margin-top 100px
		&:last-child
			margin-bottom 100px
		.container
			width 260px
		.img
			height 135px
		&.active
			.container
				left calc(50% - 150px) !important
				top calc(45% - 170px) !important
				left calc(50vw - 150px) !important
				top calc(45vh - 170px) !important
				width 300px
			.img
				height 160px
			.tags
				top 170px
			.text
				margin-top 70px


</style>