<template lang="pug">

.scrollblock
	.views(v-mousewheel="scroll")
		.view(v-for="index in number" :key="index - 1" :class="{active: active == index - 1, hidden: active > index - 1}")
			slot(:name="index - 1")

	.scrollbar(v-if="params.scrollBar !== false" @click="scrollBar")
		.caret(:style="{top: active / number * 100 + '%', height: 100 / number + '%'}")

	.nav(v-if="params.nav !== false")
		.next(:class="{hidden: active > number - 2}" @click="active = active < number - 1 ? active + 1 : number - 1")
			slot(name="next") Далее
		.back(:class="{hidden: active < 1}" @click="active = active > 0 ? active - 1 : 0")
			slot(name="back") Назад

	slot(name="other")

</template>

<script>
import { mousewheel } from 'plugins/directives'

export default {
	props: ['params'],
	data(){
		return {
			activeView: 0,
			last: {
				time: 0,
				delta: 0
			}
		}
	},
	methods: {
		scroll(e){
			if ((
					e.timeStamp - this.last.time < 80
					&& (
						(e.deltaY > 0 && this.last.delta > 0)
						||
						(e.deltaY < 0 && this.last.delta < 0)
					)
				) || (
					e.timeStamp - this.last.time < 1000
					&&
					(
						(e.deltaY > 0 && e.deltaY <= this.last.delta)
						||
						(e.deltaY < 0 && e.deltaY >= this.last.delta)
					)
				))
				return

			this.last.time = e.timeStamp
			this.last.delta = e.deltaY

			if ((e.deltaY > 0) && this.active < this.number - 1) this.active++
			if ((e.deltaY < 0) && this.active > 0) this.active--
		},
		scrollBar(e){
			let proc = e.layerY / e.target.offsetHeight

			for (let i = 0; i < this.number; i++)
				if (i / this.number < proc && (i+1) / this.number > proc) {
					this.active = i
					return
				}
		},
	},
	computed: {
		number(){
			return this.params.number
		},
		active: {
			get(){
				return this.activeView
			},
			set(val) {
				this.activeView = val
				this.$emit('changeView', val)
			}
		}
	},
	watch: {
		number(number){
			if (this.active > number - 1)
				this.active = number - 1
		}
	},
	mixins: [mousewheel]
}

</script>

<style lang="stylus">
.scrollblock
	position relative
	width 100%
	height 100%

	.views
		position absolute
		height 100%
		width 100%

	.view
		position absolute
		width 100%
		height 100%
		top 100%
		transition top .5s
		&.active
			top 0
		&.hidden
			top -100%
			top -100vh

	.scrollbar
		position absolute
		right 8px
		width 8px
		height 500px
		top calc(50vh - 250px)
		cursor pointer
		&::after
			content ''
			position absolute
			z-index 1
			width 8px
			height 100%
			top -2px
			left -1px
			border-radius 10px
			padding 2px 1px
			box-sizing content-box
			background-color rgba(#fff, .2)
			box-shadow inset 0 0 4px rgba(#000, .6)
			// border 1px solid rgba(#fff, .1)
		.caret
			position absolute
			z-index 2
			width 6px
			left 1px
			border-radius 10px
			background-color rgba(#fff, .5)
			transition top .5s

	.nav
		.next, .back
			position absolute
			font-size 30px
			cursor pointer
			transform translateX(-50%) scale(1)
			transition transform .5s
			left 50%
			text-align center
			&.hidden
				display none
			&:hover
				transform translateX(-50%) scale(1.1)
		.next
			bottom 90px
		.back
			top 90px

@media screen and (max-width: 800px)
	.scrollblock
		.nav
			.next, .back
				font-size 22px
			.next
				bottom 50px
				top auto
			.back
				bottom auto
				top 60px

</style>